#battle-theme {
  bottom: 0px;
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 5px;
  color: #fff; 
  image-rendering: pixelated;
  z-index: 10;
  font-family: 'Tourney', cursive;
  width: 30vw;
  text-align: center;
  .btn {
    font-size: 14px;
    color: #fff;
  }
  .btn:hover {
    background-color: #d27d2c;
  }
  
  
}